import * as React from "react";
import * as ReactDom from "react-dom";
import { Version } from "@microsoft/sp-core-library";
import {
  IPropertyPaneConfiguration,
  PropertyPaneTextField
} from "@microsoft/sp-property-pane";
import { BaseClientSideWebPart } from "@microsoft/sp-webpart-base";

import * as strings from "RevlonProjectTrackerWebPartStrings";
import { RevlonProjectTracker, IRevlonProjectTrackerProps } from "./components/RevlonProjectTracker";
import { ProjectService } from "../../services/ProjectService";
import { IProjectService } from "../../services/IProjectService";

export interface IRevlonProjectTrackerWebPartProps {
  description: string;
}

export default class RevlonProjectTrackerWebPart extends BaseClientSideWebPart<IRevlonProjectTrackerWebPartProps> {
  private projectService: IProjectService;

  protected onInit(): Promise<void> {
    this.projectService = new ProjectService(this.context);
    return super.onInit();
  }

  public render(): void {
    const element: React.ReactElement<IRevlonProjectTrackerProps> = React.createElement(
      RevlonProjectTracker,
      {
        projectService: this.projectService
      }
    );

    ReactDom.render(element, this.domElement);
  }

  protected onDispose(): void {
    ReactDom.unmountComponentAtNode(this.domElement);
  }

  protected get dataVersion(): Version {
    return Version.parse("1.0");
  }

  protected getPropertyPaneConfiguration(): IPropertyPaneConfiguration {
    return {
      pages: [
        {
          header: {
            description: strings.PropertyPaneDescription
          },
          groups: [
            {
              groupName: strings.BasicGroupName,
              groupFields: [
                PropertyPaneTextField("description", {
                  label: strings.DescriptionFieldLabel
                })
              ]
            }
          ]
        }
      ]
    };
  }
}

